* {
  box-sizing: border-box;
}
html,
body {
  background-color: blue;
}

.moving-text {
  white-space: nowrap;
  animation: moveText 5s linear infinite;
  font-size: 20rem;
  color: blue;
  margin: 0;
  padding: 0;
}
.test {
  transform: rotate(90deg);
}
@keyframes moveText {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}
